.card {
   border: 1px solid #f3f3f3;
   padding: 20px;
   width: 220px;
   border-radius: 40px;
   margin-bottom: 30px;
   transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
   span {
      font-size: 13px;
      opacity: 0.5;
      text-transform: uppercase;
   }
   b {
      font-size: 12px;
   }
   h5 {
      font-weight: 400;
      font-size: 14px;
   }
   .plus {
      cursor: pointer;
   }
   margin-right: 30px;
   .favorite {
      position: absolute;
      cursor: pointer;
   }
   &:hover {
      box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
      transform: translateY(-5px);
   }
}
